<template>
<div class="header">
    <ul class="header-button-left">
      <li>Cancel</li>
    </ul>
    <ul class="header-button-right">
      <li @click="step++" v-if="step == 1">Next</li>
      <li @click="publish" v-if="step == 2">Publish</li>
    </ul>
</div>

<Container @sendText="text = $event" :url="url" :postingData="postingData" :step="step"></Container>

<button @click="more">더보기</button>

<div class="footer">
  <ul class="footer-button-plus">
    <input @change="upload" type="file" id="file" class="inputfile" />
    <label for="file" class="input-plus">+</label>
  </ul>
</div>

<h2>안녕 {{$store.state.example}}</h2>
<h2>{{$store.state.age}}</h2>
<button @click="plusplus(9)">+1 to Age!</button>
<p>{{age}} {{name}}</p>
</template>

<script>
import Container from './components/Container.vue';
import postingData from './assets/postingdata.js';
import axios from 'axios';
import {mapMutations, mapState} from 'vuex';

export default {
  name: 'App',
  data () {
    return {
      text : "",
      url : "",
      step : 0,
      moreButtonCount : 0,
      postingData : postingData,
      appfilter : 0
    }
  },
  components: {
    Container : Container,
  },
  computed : {
    ...mapState(['name', 'age', 'likes'])
  },
  methods: {
    ...mapMutations({plusplus : 'plusAge'}),
    more() {
      axios.get(`https://codingapple1.github.io/vue/more${this.moreButtonCount}.json`)
      .then((result) => {this.postingData.push(result.data)})
      .catch(() => {console.log("No more new feeds!")})
      this.moreButtonCount++;
    },
    upload(event) {
      let file = event.target.files;
      console.log(file)
      this.url = URL.createObjectURL(file[0]);
      this.step++;
    },
    publish() {
      let myPhoto = {
        name: "Minjae",
        userImage: "url(https://placeimg.com/100/100/arch)",
        postImage: this.url,
        likes: 1000,
        date: "May 15",
        liked: false,
        content: this.text,
        filter: this.appfilter,
      };
      console.log(myPhoto);
      postingData.unshift(myPhoto);
      this.step = 0;
    }
  },
  mounted() {
    this.emitter.on("applyFilterApp", (a)=>{
      console.log(a);
      this.appfilter = a;
    })
  }
}
</script>

