<template>
  <div :class="`filter-item ${filter}`" :style="`background-image : url(${photoUrl})`">
    <slot><span style="color: black">{{filter}}</span></slot>
    <button @click="fire">Apply</button>
    
</div> 
  
</template>

<script>
export default {
    props : {
        photoUrl : String,
        filter: String,
    },
    methods : {
        fire() {
            this.emitter.emit('applyFilter', this.filter);
        }
    }
}
</script>

<style>
.filter-item {
  width: 100px;
  height: 100px;
  margin: 10px 10px 10px auto;
  padding: 8px;
  display: inline-block;
  color : white;
  background-size: cover;
  background-position : center;
}
</style>