import { createStore } from "vuex";

const store = createStore({
    state() {
        return {
            name: 'KING LORD WHAT',
            example: "PARK JI SUNG",
            age: 13,
            firstPostLikes: 53,
            firstPostLiked: false
        }

    },
    mutations: {
        plusAge(state, number) {
            state.age += number;
        },
        likeUnlike(state) {
            if (state.firstPostLiked == true) {
                state.firstPostLikes--;
                state.firstPostLiked = false;
            } else {
                state.firstPostLikes++;
                state.firstPostLiked = true;
            }
        }
    }
})

export default store;